<style lang="scss">
  .layout {
    p {
      svg {
        height: 1.5em;
        vertical-align: sub;
      }
      a {
        color: inherit;
      }
    }
    .log {
      position: relative;
      padding-left: 6em;
      margin-bottom: .5em;
      font-size: 12px;
      .date {
        position: absolute;
        left: 0; top: 3px;
      }
      p {
        font-size: 12px;
        margin: 0;
        &::before {
          content: '';
          display: inline-block;
          width: 5px;
          height: 5px;
          background: #777;
          transform: rotate(45deg);
          vertical-align: middle;
          margin-right: 5px;
          margin-top: -3px;
          border: 2px solid #777;
          border-color: #111 #444 #666 #999;
        }
      }
    }
  }
</style>

<template>
  <div class="layout">
    <h2><span>Layout</span> <span class="chinese"></span></h2>
    <p>.</p>
    <h3><span>Download</span></h3>
    <p><a href="https://github.com/barusu/web/tree/master/static/layout" target="blank"><o-svg type="github"></o-svg>GayHub</a></p>
    <h3><span>Use</span></h3>
    <p>iframe.</p>
    <p>&lt;<o-text type="tag">iframe</o-text> src="/static/index.html#index?id=<o-text type="key" title="ID">Identification</o-text>" frameborder="0" width="100%" <o-text type="key" title="">height="500"</o-text>&gt;&lt;/<o-text type="tag">iframe</o-text>&gt;</p>
    <p><o-text type="title">Host</o-text></p>
    <p>hostindex.html&lt;head/&gt;host.</p>
    <p><o-text type="title"></o-text></p>
    <p>iframe,,.</p>
    <p><o-text type="title"></o-text></p>
    <p>iframe,.</p>
    <p><o-text type="title"></o-text></p>
    <p>iframe,.<o-text type="key">,</o-text></p>
    <p><o-text type="title"></o-text></p>
    <p>iframe,.<o-text type="key">,</o-text></p>
    <iframe src="/static/layout/index.html#index?id=02" frameborder="0" width="100%" :height="height" ref="iframe"></iframe>
    <h3><span>Update</span></h3>
    <div class="log" v-for="l in log">
      <o-text type="time" class="date">{{l.date}}</o-text>
      <o-list class="diamond" :list="l.log"></o-list>
    </div>
  </div>
</template>

<script>
  const log = [
    {
      date: '2017-01-22',
      log: [
        {text: '', status: 'forgive'},
        {text: 'ID', status: 'forgive'},
        {text: '', status: 'forgive'}
      ]
    }, {
      date: '2017-01-18',
      log: [
        {text: '', status: 'forgive'},
        {text: '', status: 'forgive'}
      ]
    }, {
      date: '2017-01-17', log: [
        {text: ',', status: 'forgive'},
        {text: '', status: 'forgive'},
        {text: 'ID,', status: 'error'},
        {text: ',', status: 'error'}
      ]
    }
  ];

  export default {
    data() {
      return {
        height: 500,
        log: log
      };
    },
    methods: {
      resize() {
        if(this.$refs.iframe) {
          this.height = this.$refs.iframe.contentDocument.body.clientHeight || this.$refs.iframe.contentDocument.documentElement.scrollHeight;
        }
        setTimeout(this.resize, 500);
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.resize();
      });
    }
  }
</script>



